<template>
  <div class="fullText"> 
    {{ getText() }}
    <span class="info" v-if="hasInformationToShowOrHide()">
      <button class="label label-primary" v-on:click="changeText" v-if="full">Ver Menos</button>
      <button class="label label-primary" v-on:click="changeText" v-else>Ver mais</button>
    </span>
  </div>
</template>

<script>
  export default {
    name: 'Excerpt',
    data: function() {
      return {
        full: false
      }
    },
    props: {
      message: String, 
      length: Number
    }, 
    methods: {
      textIsLong: function(message, length) {
        return (message.length>length+5);
      },
      changeText: function() {
        this.full = !this.full;
      },
      getText: function() {
        let message = this.message
        let length = this.length
        if (length==null) length = 120
        if (this.textIsLong(message, length)) {
          if (!this.full) {
            return message.substring(0, length)+"..."
          }
          return message
        }
        return message
      }, 
      hasInformationToShowOrHide: function() {
        let length = this.length
        if (length==null) length = 120
        if (this.textIsLong(this.message, length)) {
          return true
        }
        return false
      }
    }
  }
</script>

