<template>
  <div id="app" class="page municipio">
    <Loading :hide="hide"></Loading>
    <div class="container">
      <div class="page-content">
        <section class="showcase" v-if="municipio">
          <div class="row mb-5">
            <div class="col-8 col-sm-9 col-md-9 col-lg-10">
              <h2>{{municipio.nome}} - {{municipio.uf}}</h2> 
            </div>
            <div class="col-4 col-sm-3 col-md-3 col-lg-2">
              <select name="ano" ref="selectAno" class="form-control form-control-lg ano"
                v-on:change="onChangeYear">
                <option v-for="anoC in (anoAtual-2007)" 
                  v-bind:key="(anoAtual-anoC+1)" 
                  :selected="(anoAtual-anoC+1) == ano ? true : false" >
                  {{(anoAtual-anoC+1)}}
                </option>
              </select>
            </div>
          </div>
          <div class="graficos box mb-5" v-if="parlamentares">
            <div class="row">
              <div class="col">
                <BarChart
                  :data="mountChartData(parlamentares)"
                  :options="{responsive: true, maintainAspectRatio: true}">>
                </BarChart>
              </div>
              <div  class="col">

              </div>
            </div>
          </div>
          <div class="parlamentares" v-if="parlamentares">
            <div class="parlamentar box mb-5" v-for="parlamentar in parlamentares" v-bind:key="parlamentar.nome">
              <div class="clearfix mb-1">
                <div class="row">
                  <div class="col-8 col-md-10">
                    <h4>{{parlamentar.nome}}</h4>
                    <h6>{{parlamentar.totalEmendas}} projetos</h6>
                    <h6>R$ {{currencyFormat(parlamentar.valorTotalRepasseEmenda)}}</h6>
                  </div>
                  <div class="col-4 col-md-2">
                    <div class="pull-right">
                      <PieChart
                        :data="mountChartParlamentarVersusMunicipio(parlamentar, parlamentares)"
                        :options="{responsive: false, maintainAspectRatio: false, legend: {display: false}}"
                        :width="100" :height="100">
                      </PieChart>
                    </div>
                  </div>
                </div>
              </div>
              <h6>Projetos do Ano</h6> 
              <table class="table table-condensed table-responsive" v-if="parlamentar.emendas">
                <thead class="thead-light">
                  <tr>
                    <th></th>
                    <th>Processo</th>
                    <th>Objeto</th>
                    <th class="d-none d-sm-table-cell">Proponente</th>
                    <th class="d-none d-md-table-cell">Pedido (R$)</th>
                    <th class="d-none d-xs-table-cell">Contrapartida (R$)</th>
                    <th>Repasse (R$)</th>
                  </tr>
                </thead>
                <tbody>
                  <tr v-if="parlamentar.emendas.length==0" class="table-warning">
                    <td colspan="7" style="text-align:center" role="">
                      <font-awesome-icon icon="spinner" pulse />
                    </td> 
                  </tr>
                  <tr v-for="emenda in parlamentar.emendas" v-bind:key="emenda.id">
                    <td>
                      <router-link :to="{ name: 'emenda', params: { id: emenda.id } }">
                        Ver
                      </router-link>
                    </td>
                    <td>{{emenda.proposta.id}}/{{emenda.proposta.anoProposta}}</td>
                    <td>
                      <Excerpt :message=emenda.proposta.descricaoObjeto></Excerpt>
                    </td>
                    <td class="d-none d-sm-table-cell">{{emenda.proposta.nomeProponente}}</td>
                    <td class="d-none d-md-table-cell">{{currencyFormat(emenda.proposta.valorGlobalProposta)}}</td>
                    <td class="d-none d-xs-table-cell">{{currencyFormat(emenda.proposta.valorContrapartidaProposta)}}</td>
                    <td>{{currencyFormat(emenda.proposta.valorRepasseProposta)}}</td>
                  </tr>
                </tbody>
              </table>
            </div>
          </div>
        </section>
        <section class="error alert alert-danger" v-else>
          <div style="text-align:center">
            <h2>Município não encontrado.</h2>
            Por favor, 
              <a href="javascript:history.go(-1)">retorne e tente novamente</a>.
          </div>
        </section>
      </div>
    </div>
  </div>
</template>

<script>
  import Loading from '@/components/Loading.vue'
  import Excerpt from '@/components/Excerpt.vue'
  import BarChart from '@/components/BarChart.vue'
  import PieChart from '@/components/PieChart.vue'

  export default {
    name: 'municipio',
    components: {
      Loading,
      Excerpt,
      BarChart,
      PieChart
    },
    props: {
      id: String,
      ano: Number
    },
    data: function () {
      return {
        municipio: false,
        parlamentares: false,
        hide: '',
        anoAtual: new Date().getFullYear()
      }
    }, 
    methods: {
      onChangeYear: function() {
        const ano = Number(this.$refs.selectAno.value)
        this.$router.replace("/municipio/"+this.id+"/"+ano)
        //this.ano = ano;
        this.hide = ''
        this.getMunicipio(this.id)
        this.getParlamentares(this.id, ano)
      },
      getMunicipio: function(id) {
        if (id!=null) {
          const self = this;
          axios.get( self.$apiUrls.getMunicipioPorId(id) )
          .then((response) => {
            response.data.valorTotalRepasse = 0
            this.municipio = response.data
            this.hide = 'hide'
          });
        }
      },
      getParlamentares: function(idMunicipio, ano) {
        if ((ano==null) || (ano==undefined)) ano=this.anoAtual;
        const self = this;
        axios.get( self.$apiUrls.getParlamentaresPorMunicipioEAno(idMunicipio, ano) )
        .then((response) => {
          this.hide = 'hide'
          if (this.municipio!=false) {
            let valorTotalRepasse = Number(response.data.valorTotalRepasseEmenda);
            this.municipio.valorTotalRepasse += valorTotalRepasse;
          }
          if (Array.isArray(response.data) && (response.data.length>0) ) {
            response.data.forEach(function (parlamentar, index) {
              parlamentar.emendas = []
              axios.get( encodeURI(self.$apiUrls.getConveniosPorParlamentarEAno(parlamentar.nome, ano)) )
              .then((response) => {
                parlamentar.emendas = response.data;
              });
            });
          }
          self.parlamentares = response.data
          return true
        }, (error) => {
          this.parlamentares = false
          console.log(error)
          return false
        });
      },
      mountChartData: function(parlamentares) {
        return {
          datasets: [{
              labels: parlamentares.map(function(parlamentar){return parlamentar.nome}),
              data: parlamentares.map(function(parlamentar){return parlamentar.valorTotalRepasseEmenda}),
              backgroundColor: ['#F44336', '#E91E63','#9C27B0','#673AB7','#3F51B5','#2196F3','#03A9F4',
                '#00BCD4','#009688','#4CAF50',"#8BC34A",'#CDDC39','#FFEB3B','#FFC107','#FF9800',
                '#FF5722','#795548'
              ]
          }],
          labels: parlamentares.map(function(parlamentar){return parlamentar.nome})
        };
      },
      mountChartParlamentarVersusMunicipio(parlamentar, parlamentares) {
        let prevVal = 0;
        let total = parlamentares.reduce(function(preVal, elem){
          return prevVal + elem.valorTotalRepasseEmenda;
        }, 0);
        return {
          datasets: [{
              data: [parlamentar.valorTotalRepasseEmenda, total],
              backgroundColor: ['#F44336', '#DDDDDD']
          }],
          labels: [parlamentar.nome, "Total"]
        };
      },
      currencyFormat(value) {
        let val = (value/1).toFixed(2).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
      } 
    },
    mounted: function() {
      this.getMunicipio(this.id)
      this.getParlamentares(this.id, this.ano)
    }
  }
</script>

<style scoped>
  .pull-right {
    text-align: right;  
  }
</style>
